<template>
  <div class="content">
    <el-row class="title">
    办会人信息与企业信息确认
    </el-row>
    <el-row class="clientClass">
      <el-radio-group size="large" v-model="filterData.deal_status">
        <el-radio-button  label="1">成交客户({{listStatus.deal_num}})</el-radio-button>
        <el-radio-button  label="0">未成交客户({{listStatus.no_deal_num}})</el-radio-button>
      </el-radio-group>
    </el-row>
    <el-row class="statusClass">
      <el-radio-group  v-model="filterData.status">
        <el-radio-button label="0">全部({{listStatus.total_num}})</el-radio-button>
        <el-radio-button label="1">待处理({{listStatus.pending_num}})</el-radio-button>
        <el-radio-button label="2">审核中({{listStatus.audit_num}})</el-radio-button>
        <el-radio-button label="3">已完成({{listStatus.complete_num}})</el-radio-button>
      </el-radio-group>
    </el-row>
    <el-row class="listTable" v-loading="viewControl.loadingList">
      <transition-group tag="div" name="roll" @after-leave="itemLive">
        <template v-for="(item,index) in listData">
            <el-table
              v-show="item.isShow"
            :key="index+'table'"
            cell-click
            :data="[item]"
            border
            style="width: 100%;text-align:center;">
              <el-table-column
                v-if="filterData.status==0"
                prop="status"
                label="状态"
                width="80">
                <template scope="scope">
                  <span class="curP" @click="getPhoneNumber(item.id,index)">
                    {{item.status==1?'待处理':''}}
                    {{item.status==2?'审核中':''}}
                    {{item.status==3?'已完成':''}}
                  </span>
                </template>
              </el-table-column>
          <el-table-column
            prop="number"
            label="客户手机号码"
            width="180">
            <template scope="scope">
              <span class="curP" @click="getPhoneNumber(item.id,index)">{{item.mobile}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="require_user_name"
            label="客户姓名历史数据"
            width="360">
          </el-table-column>
          <el-table-column
            prop="require_company_name"
            label="企业名称历史数据">
          </el-table-column>
        </el-table>
            <el-row
              v-show="item.isShow"
              :key="index+'info'">
            <el-form :model="listData[index]" :rules="ruler" :ref="`form${index}`" :inline="true">
            <el-row class="clientInfo">
              <el-form-item class="infoItem " prop="first_name"  label="客户姓名">
                <el-input style="width:57px" class="requireInput" v-model="item.first_name" placeholder="姓"></el-input>
                <el-input style="width:57px;"  class="noError " v-model="item.last_name" placeholder="名"></el-input>
              </el-form-item>
              <el-form-item prop="en_name" >
                <el-input style="width:99px;padding-left:10px;" v-model="item.en_name" placeholder="英文名"></el-input>
              </el-form-item>
              <el-form-item class="infoItem requireInput"  prop="company_name"   label="客户所在公司">
                <el-input style="width:237px" v-model="item.company_name" placeholder="公司全称"></el-input>
              </el-form-item>
              <el-form-item class="infoItem" prop="group_company_name"   label="客户集团总公司">
                <el-input style="width:237px" v-model="item.group_company_name" placeholder="公司全称"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button :disabled="item.isSubmiting" :loading="viewControl.isSubmiting" @click="submitItem(index)" type="primary">确认</el-button>
              </el-form-item>
            </el-row>
          </el-form>
          </el-row>
        </template>
      </transition-group>
      <el-row class="emptyList" v-if="listData.length<=0&&!viewControl.loadingList">
        暂无更多信息
      </el-row>
      <el-row class="pagerBox" v-if="!(listData.length<=0&&!viewControl.loadingList)">
        <el-pagination
          @size-change="pagerSizeChange"
          @current-change="pagerChange"
          :current-page="listStatus.current_page"
          :page-size="20"
          layout="total, prev, pager, next"
          :total="listStatus.total"></el-pagination>
      </el-row>
    </el-row>

  </div>
</template>
<script>
  import {
    Radio,
    RadioButton,
    RadioGroup,
    Row,
    Badge,
    FormItem,
    Form,
    Input,
    Button,
    Pagination,
    Notification
  } from 'element-ui';
  import Vue from 'vue'
  Vue.prototype.$notify=Notification
  export default {
    name: 'clearData',
    data: function () {
      return {
        viewControl:{
          loadingList:false,
          isSubmitting:false
        },
        filterData:{
          //过滤条件
          deal_status:'1',
          status:'0',
        },
        custerName:['',''],
        listStatus:{
          current_page: 1,
          total: 1,
          status: 1,
          deal_status: 2,
          deal_num: 0,
          no_deal_num: 0,
          total_num: 0,
          pending_num: 0,
          audit_num: 0,
          complete_num: 0,
        },
        listData:[
          {
            id: 1,
            mobile: "1521***284",
            require_user_name: "张三,李四",
            require_company_name: "会小二公司,会小三公司,会小四公司",
            first_name: "",
            last_name: "三",
            en_name: "",
            company_name: "",
            group_company_name: ""
          },
          {
            id: 2,
            mobile: "1521***284",
            require_user_name: "张五",
            require_company_name: "会小五公司",
            first_name: "",
            last_name: "",
            en_name: "",
            company_name: "会小五公司",
            group_company_name: "会小五公司"
          }
        ],
        ruler:{
          first_name:[{ required: true, message: '请输入客户的姓氏',trigger: 'blur'  }],
          company_name:[{ required: true, message: '请输入客户所在公司',trigger: 'blur'  }],
        }
      }
    },
    mounted() {
      this.getListData()
    },
    methods: {
      getListData(){
        let self = this;
        let filter = self.filterData
        filter.current_page = self.listStatus.current_page
        self.viewControl.loadingList = true;
        self.$ajax.get(self.$api.getModuleHost('CRM', "PretreatmentCustomer/index"),
          filter)
          .then(res => {
          for(let p in self.listStatus){
            self.listStatus[p]=res[p];
          }
             self.listData=[];
            for (let i = 0 ;i < res.data.length ; i++){
              res.data[i].isShow = true;
              res.data[i].isSubmiting = false;
            }
            //组件会缓存之前的验证状态。
             setTimeout(function () {
               self.listData=res.data
               self.viewControl.loadingList = false;
             })
          },300)
          .catch(err => {
            console.log(err)
          })
      },
      pagerSizeChange(){
      },
      pagerChange(index){
        this.listStatus.current_page = index;
        this.getListData();
      },
      getPhoneNumber(id,index){
        let self = this;
        let item = self.listData[index];
        let mobile = item.mobile
        if (mobile.indexOf('*')==-1)
          return;
        self.$ajax.get(self.$api.getModuleHost('CRM', "PretreatmentCustomer/getMobile"),
          {id:id})
          .then(res => {
            if (res.code == 1001){
              item.mobile=mobile.replace('****',res.mobile)
            }
          })
          .catch(err => {
            console.log(err)
          })
      },
      submitItem(index){
        let self = this;
        let formName = 'form'+index
       this.$refs[formName][0].validate(error =>{
         if (error){
            let currData = self.listData[index]
           self.listData[index].isSubmiting=true;
           self.viewControl.isSubmiting = true;
           self.$ajax.post(self.$api.getModuleHost('CRM', "PretreatmentCustomer/update"),
             {
               id:currData.id,
               first_name: currData.first_name,
               last_name: currData.last_name,
               en_name: currData.en_name,
               company_name: currData.company_name,
               group_company_name: currData.group_company_name,
             })
             .then(res => {
               if (res.code == 1000){
                 let status = res.status;
                 let currStatus ;
                 if(self.filterData.status==0){
                   currStatus = self.listData[index].status;
                   self.listData[index].status = status;
                 }else {
                   currStatus = self.filterData.status;
                 }
                 console.log(self.filterData.status,currStatus)
                 if (status != currStatus){
                   switch (Number(status)){
                     case 0:
                       break;
                     case 1:
                       self.listStatus.pending_num++;
                       break;
                     case 2:
                       self.listStatus.audit_num++;
                       break;
                     case 3:
                       self.listStatus.complete_num++;
                       break;
                   }
                   switch (Number(currStatus)){
                     case 0:
                       break;
                     case 1:
                       self.listStatus.pending_num--;
                       self.listData[index].isShow = false;
                       break;
                     case 2:
                       self.listData[index].isShow = false;
                       self.listStatus.audit_num--;
                       break;
                     case 3:
                       self.listData[index].isShow = false;
                       self.listStatus.complete_num--;
                       break;
                   }
                 }
                 self.$notify({
                   title: '确认成功',
                   message: '客户信息修改成功',
                   type: 'success'
                 })
               }else {
                 self.$notify({
                   title: res.msg,
                   message: res.msg,
                   type: 'warning'

                 })
               }
               self.viewControl.isSubmiting = false;
             })
             .catch(err => {
               self.viewControl.isSubmiting = false;
               self.listData[index].isSubmiting=false;
               console.log(err)
             })
         }

       })
      }
    },
    watch:{
      'filterData':{
        handler:function (val, oldVal) {
          this.listStatus.current_page = 1;
          this.getListData()
        },
        deep:true
      }
    },
    components:{
      ElButton:Button,
      ElInput:Input,
      ElForm:Form,
      ElFormItem:FormItem,
      ElBadge:Badge,
      ElRow:Row,
      ElPagination:Pagination,
      ElRadioButton:RadioButton,
      ElRadioGroup:RadioGroup,
      ElRadio:Radio,
      ELNotification:Notification
    }
  }
</script>
<style lang="less">
  .content{
    padding:30px;
  }
  .title{
    font-size:20px;
    padding-bottom:20px;
  }
  .clientClass{
    padding-bottom:15px;
    border-bottom:1px solid #1D8CE0;
  }
  .statusClass{
    padding:15px 0;
  }
  .listTable{
    width:1150px;
    .el-table{
      th{
        text-align:center;
      }
    }
  }
  .el-form-item{
    margin-bottom:0;
  }
  .clientInfo{
    padding:15px 5px 15px 5px;
    border:1px solid #dfe6ec;
    border-top:none;
    margin-bottom:15px;
    .infoItem{
    }
  }
  .pagerBox{
    padding:15px 0;
  }
  .el-pagination{
    display:inline-block;
    float:right;
  }
  .el-form-item.is-required .el-form-item__label:before{
    display:none;
  }
  .noError.el-input .el-input__inner{
    border-color:#bfcbd9;;
  }
  .emptyList{
    text-align:center;
    font-size:16px;
    line-height:100px;
    min-height:100px;
  }
  .curP{
    cursor:pointer;
  }
.roll-leave-active {
    animation: roll .6s;
  }
  @keyframes roll {
    from {
      opacity: 1;
    }

    to {
      opacity: 0;
    }
  }
  .requireInput{
    :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
      color: red; opacity:1;
    }

    ::-moz-placeholder { /* Mozilla Firefox 19+ */
      color: red;opacity:1;
    }

    input:-ms-input-placeholder{
      color: red;opacity:1;
    }

    input::-webkit-input-placeholder{
      color: red;opacity:1;
    }
  }
</style>



